import { css, styled } from '../../utils/styled';
import { outlineStyle } from '../../utils/styles';
import { color, fontFamily, modifier, spacing, typography } from '../../utils/theme';
import { CheckboxCheckedFilled } from './icons/CheckboxCheckedFilled';
import { CheckboxEmpty } from './icons/CheckboxEmpty';
import { CheckboxIndeterminateFilled } from './icons/CheckboxIndeterminateFilled';

export const Container = styled.div`
    position: relative;
    display: grid;
    grid-auto-flow: row;
    gap: ${spacing(1)};
    width: 100%;
`;

export const Label = styled.label`
    display: grid;
    grid-template-columns: min-content;
    grid-auto-flow: column;
    gap: ${spacing(1)};
    font-family: ${fontFamily('default')};

    /* Required because of global styles applied by bootstrap */
    margin: 0;
`;

export interface CheckboxIconProps {
    checked?: boolean;
    indeterminate?: boolean;
}

export const iconSize = '20px';

export const CheckboxDefault = styled.svg.attrs<CheckboxIconProps>(({ checked, indeterminate }) => {
    if (indeterminate) {
        return { as: CheckboxIndeterminateFilled };
    }
    if (checked) {
        return { as: CheckboxCheckedFilled };
    }

    return { as: CheckboxEmpty };
})<CheckboxIconProps>`
    rect:first-child {
        stroke: ${color('content.secondary')};
    }
    font-size: ${iconSize};

    ${modifier(
        ({ checked, indeterminate }: CheckboxIconProps) => checked || indeterminate,
        css`
            color: ${color('primary.500')};
            rect:first-child {
                stroke: ${color('primary.500')};
            }
            path {
                stroke: none;
            }
        `,
    )};

    ${Container}:hover &,
    [data-dev-state='hover'] & {
        color: ${color('interactive.primary.hover')};
        rect:first-child {
            stroke: ${color('interactive.primary.hover')};
        }
    }
    ${Container}:active &,
    [data-dev-state='active'] & {
        color: ${color('interactive.primary.pressed')};
        rect:first-child {
            stroke: ${color('interactive.primary.pressed')};
        }
    }
`;

export const Input = styled.input`
    && {
        // increases the specificity of the selector to solve the problem with conflicting styles from Bootstrap.
        position: absolute !important; // !important is necessary because it conflicts with bootstrap styles.
        opacity: 0;
        outline: none;
    }

    /*
    * Focus outline is only visible when user is using keyboard. This behavior matches native radio behavior.
    */
    &:focus-visible + svg {
        ${outlineStyle}
    }
`;

export const Text = styled.span`
    ${typography('description')};
    color: ${color('content.primary')};
    cursor: default;
`;

export const MultiCheckboxContainer = styled.div`
    width: 100%;
`;

export const MultiCheckbox = styled.div`
    display: grid;
    gap: ${spacing(3)};
`;

export const MultiCheckboxErrorContainer = styled.div`
    padding-top: ${spacing(3)};
    display: grid;
    gap: ${spacing(1)};
`;
